import Event from './Event';

const Events = ({events}) => {

    const eventsId = Object.keys(events);

    return (  
        <div className="list-group">
            { eventsId.map( key => (
                <Event
                    key={key}
                    event={events[key]}
                />
            ) ) }
        </div>
    );
}
 
export default Events;